<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus consultas
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Consultas pendientes</v-col>
      </v-row>

      <v-row class="text-center">
        <v-col>
          <div class="mb-2" style="color: #084068">{{ consultation.service.name }}</div>

          <div v-if="consultation.status_id !== 6" class="call-to-action">
            <v-row>
              <v-col class="text-center">
                <div>Costo del documento : S/. {{ consultation.price }}</div>
                <div>Código de solicitud: {{ consultation.id | padleft }}</div>
                <div>Abogado responsable: {{ consultation.lawyer.name }}</div>
                <div v-if="consultation.service_id === 2" class="videoCall">
                  <br>
                  <div>Fecha: {{ consultation.date }}</div>
                  <div>Hora: {{ consultation.time }}</div>
                  <div>Meeting URL: <a :href="consultation.meeting">{{ consultation.meeting }}</a></div>
                </div>
              </v-col>
            </v-row>
          </div>
        </v-col>
      </v-row>

      <chat v-if="consultation.status_id !== 6"
            key-field="consultation_id"
            :key-value="consultationId"
            :event-name="'consultation-id-'+consultationId"
            event-class="App\Events\Consultation\NewMessage"
            :get-messages-url="getMessagesUrl"
            :post-messages-url="postMessagesUrl"></chat>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex"
import axios from "axios"
import Chat from '../../components/Chat'
import moment from 'moment'

export default {
  name: 'RequestDetail',
  components: {Chat},
  data: () => ({
    consultation: {
      id: '',
      service: {
        name: ''
      },
      lawyer: '',
      price: ''
    },
    specialties: [],
    departments: [],
    form: {
      specialty_id: '',
      department_id: '',
    },
    logs: [],
  }),
  computed: {
    ...mapGetters(['user']),
    consultationId() {
      return this.$route.params.id
    },
    getMessagesUrl() {
      return '/consultations/' + this.consultationId + '/messages'
    },
    postMessagesUrl() {
      return '/consultations/' + this.consultationId + '/messages'
    },
  },
  created() {
    this.loadConsultation()
    this.loadSpecialties()
    this.loadDepartments()
  },
  mounted() {
    this.getWelcomeMessage();
    this.loadLogs();
  },
  methods: {
    loadConsultation() {
      let url = 'consultations/' + this.consultationId
      axios.get(url).then((response) => {
        this.consultation = response.data
      })
    },
    loadSpecialties() {
      let url = 'specialties'
      axios.get(url).then((response) => {
        this.specialties = response.data
      }).catch().then(() => {

      })
    },
    loadDepartments() {
      let url = 'ubigeo/departments'
      axios.get(url).then((response) => {
        this.departments = response.data
      }).catch().then(() => {

      })
    },
    loadLogs() {
      axios.get('/logs?model_id=' + this.consultationId + '&model_type=App\\Consultation').then((response) => {
        this.logs = response.data;
      });
    },
    date(date) {
      return moment(date);
    },
    getWelcomeMessage() {
      axios.get('/consultations/' + this.consultationId + '/messages/welcome')
    },
  }
}
</script>
